	module.exports = {"data":{"contentfulChapitre":{"titreChapitre":"Chapitre 12 - La vérité","texte":{"childMarkdownRemark":{"html":"<p>Il est à peu près 6 heures du matin quand j’entends Clara se lever. Je décide de me lever aussi et d’aller la voir, comme j’ouvre la porte de ma chambre, Clara est juste devant sur le point de cogner.</p>\n<p>Wamien : Oh salut.<br>\nClara : Salut, je ne pensais pas que tu étais réveillé.<br>\nWamien : Je ne dors pas beaucoup.</p>\n<p>Je souris. C’est vrai que je dors beaucoup moins qu’avant, je mange beaucoup moins aussi.</p>\n<p>Clara : Moi quand je dors, je dors comme un bébé.</p>\n<p>Elle rit, comme un ange, la voix douce en tout temps. J’en rougis.</p>\n<p>Clara : Oh tiens, une couleur sur ta peau. C’est mignon.<br>\nWamien : Arrête, ce n’est pas voulu.</p>\n<p>On rit pendant un instant. Je perds mon sourire quand je pense que je devrais lui parler de ce que je suis réellement.</p>\n<p>Wamien : Clara? Je, j’ai quelque chose à te dire.</p>\n<p>Je regarde ailleurs en avalant ma salive. Elle me sourit, elle est tellement belle.</p>\n<p>Clara : Je pense savoir, dit-elle avec un ton suave.<br>\nWamien : Ah bon? Je la regarde surpris.<br>\nClara : Oui, juste à te voir agir, ça se voit tout de suite.</p>\n<p>Elle s’approche lentement vers moi en souriant.</p>\n<p>Wamien : Tu sais que je suis un cyborg?</p>\n<p>Elle change d’expression faciale tout d’un coup et recule un peu.</p>\n<p>Clara : Tu es quoi? Non, ce n’est pas possible.<br>\nWamien : Je suis un cyborg, je heum je ne suis pas 100 % humain comme toi.<br>\nClara : C-comment ça?<br>\nWamien : Je ne sais pas. C’est comme ça et c’est tout, je sais que j’étais humain avant, mais je ne sais pas ce qui a provoqué ce changement.<br>\nClara : Je... je peux voir?</p>\n<p>Je relève la manche de mon t-shirt et baisse la peau laissant voir les pièces et les plaques de métal de mon bras. Clara regarde la bouche entre-ouverte et approche sa main.</p>\n<p>Clara : Wow... Et, et pour ton visage.<br>\nWamien : Juste l’œil droit, le reste est humain.<br>\nClara : Oh d’accord. Je, je peux toucher?</p>\n<p>Je hoche la tête doucement et elle approche sa main sur les plaques de métal qui constituent mon bras et je souris, content de voir que sa réaction n’est pas aussi négative que je l’aurais pensé.</p>\n<p>Clara : Wamien?...<br>\nWamien : Oui?<br>\nClara : Est-ce que c’est ceux que tu fuyais hier qui t’ont fait ça?<br>\nWamien : Oui.<br>\nClara : Pourquoi tu les fuyais?<br>\nWamien : Je ferais mieux de me taire. C’est parce que j’étais dans un labo où on faisait des expériences génétiques sur les humains pour leur donner des pouvoirs.<br>\nClara : Des pouvoirs? Quel genre?<br>\nWamien : Voler, générer du feu ou de la glace, prémonition, corps ultra flexible, ce genre de truc quoi.<br>\nClara : Tu veux savoir mon secret?<br>\nWamien : Pourquoi?</p>\n<p>Clara prit un couteau et s’ouvrit un peu la paume de main</p>\n<p>Wamien : Nan, mais t’es folle!</p>\n<p>Je ne réalise pas à l’instant que sa peau est déjà en train de se refermer dans une petite lueur bleutée. Elle est capable de se guérir toute seule.</p>\n<p>Clara : Je sais me guérir toute seule, et guérir les blessures des autres.</p>\n<p>Elle rougit un peu et tourne la tête vers le bas en mordant un peu son doigt.</p>\n<p>Wamien : Comment c’est possible?<br>\nClara : Je n’en sais rien, mais je suis capable de faire ça depuis que j’ai 15 ans, ça fait cinq ans de ça.<br>\nWamien : C’est pour ça que tu bosses dans un hôpital?<br>\nClara : Et je ne sais pas pourquoi, mais j’ai l’impression de pouvoir sentir quand les gens ont quelque chose de spécial. Comme toi, j’ai senti que tu avais quelque chose de spécial.</p>\n<p>Je souris et rougis un peu face à toutes ces révélations. Elle rougit aussi en relevant un peu la tête vers moi.</p>\n<p>Wamien : Tu ne sais pas le poids que ça m’enlève de savoir que tu es comme moi.<br>\nClara : Oui.</p>\n<p>Elle s’approche doucement et se blottit dans mes bras. Je la regarde et la serre contre moi doucement. Elle lève un peu la tête pour me regarder dans les yeux.</p>"}},"chapitreAvant":"chapitre-11-la-nouvelle-maison","chapitreApres":"chapitre-13-la-rebellion-partie-1","nomRoman":"../le-premier-cyborg-tome-1","slug":"chapitre-12-la-verite"}},"pathContext":{"slug":"chapitre-12-la-verite"}}

